<template>
  <div class="pricing-container">
    <h1 class="pricing-title">Pok√©mon Card Pricing</h1>
    <div class="pricing-list" v-if="cards.length">
      <div class="pricing-card" v-for="card in cards" :key="card.id">
        <img :src="card.imageUrl" alt="Card Image" class="card-image" />
        <h2 class="card-name">{{ card.name }}</h2>
        <p class="card-price">Price: {{ card.price }} USD</p>
      </div>
    </div>
    <div v-else class="loading">Loading pricing information...</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cards: [],
    };
  },
  created() {
    this.fetchPricingData();
  },
  methods: {
    async fetchPricingData() {
      try {
        const response = await fetch('https://api.pokemontcg.io/v2/cards');
        const data = await response.json();
        this.cards = data.data.map(card => ({
          id: card.id,
          name: card.name,
          price: card.cardmarket.prices.averageSellPrice || 'N/A',
          imageUrl: card.images.small,
        }));
      } catch (error) {
        console.error('Error fetching pricing data:', error);
      }
    },
  },
};
</script>

<style scoped>
.pricing-container {
  padding: 20px;
  background-color: #1e1e1e;
  color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.pricing-title {
  font-size: 2em;
  margin-bottom: 20px;
  text-align: center;
}

.pricing-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.pricing-card {
  background-color: #2c2c2c;
  border-radius: 8px;
  margin: 10px;
  padding: 15px;
  text-align: center;
  transition: transform 0.2s;
}

.pricing-card:hover {
  transform: scale(1.05);
}

.card-image {
  width: 100px;
  height: auto;
}

.card-name {
  font-size: 1.5em;
  margin: 10px 0;
}

.card-price {
  font-size: 1.2em;
}

.loading {
  text-align: center;
  font-size: 1.2em;
  color: #888888;
}
</style>